<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process5"></a>

<p><br></p>
<p><span class="rvts17">游戏界面解析：</span></p>
<p class="rvps2"><span class="rvts29">开始菜单：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber6.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">进入到《神界</span><span class="rvts20">3</span><span class="rvts21">：原罪》的开始界面，笔者在此逐项介绍选单。</span><span class="rvts20"></span><span class="rvts21">故事</span><span class="rvts20"></span><span class="rvts21">，可以开始新的游戏，体验故事剧情。这里可以选择</span><span class="rvts20"></span><span class="rvts21">单人模式</span><span class="rvts20"></span><span class="rvts21">和</span><span class="rvts20"></span><span class="rvts21">多人模式</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts20"></span><span class="rvts21">继续</span><span class="rvts20"></span><span class="rvts21">，则是直接从上次游戏保存的地方开始游戏。</span>
<br><span class="rvts20"></span><span class="rvts21">游戏大师</span><span class="rvts20"></span><span class="rvts21">，可以自定义游戏剧情并游玩，也可以在线和其他玩家一同游玩自定义剧情模组。</span>
<br><span class="rvts20"></span><span class="rvts21">继续（游戏大师）</span><span class="rvts20"></span><span class="rvts21">，可以继续上次游戏大师游玩的进度。</span>
<br><span class="rvts20"></span><span class="rvts21">竞技场</span><span class="rvts20"></span><span class="rvts21">，只能在线游玩，和其他玩家角色进行竞技</span><span class="rvts20">PK</span><span class="rvts21">。</span>
<br><span class="rvts20"></span><span class="rvts21">选项</span><span class="rvts20"></span><span class="rvts21">，可以进行有关游戏性、图像、音频、操作的设置。</span>
<br><span class="rvts20"></span><span class="rvts21">模组</span><span class="rvts20"></span><span class="rvts21">，为游戏添加其他玩家原创模组，增添游戏乐趣。</span>
<br><span class="rvts20"></span><span class="rvts21">玩家档案</span><span class="rvts20"></span><span class="rvts21">，检阅玩家存档的状况和进度。</span>
<br><span class="rvts20"></span><span class="rvts21">制作组</span><span class="rvts20"></span><span class="rvts21">，可以查看游戏制作人员的名单。</span>
<br><span class="rvts20"></span><span class="rvts21">退出游戏</span><span class="rvts20"></span><span class="rvts21">，可以退出游戏。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">难度选择：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber7.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">选择单人模式后，可以选择游戏难度。游戏难度共有四种，下面笔者来一一说明。</span>
<br><span class="rvts20"></span><span class="rvts21">探索模式</span><span class="rvts20"></span><span class="rvts21">，如果你喜欢故事与探险，并且不想被战斗难倒，请选择此模式。</span>
<br><span class="rvts20"></span><span class="rvts21">经典模式</span><span class="rvts20"></span><span class="rvts21">，经典的神界玩法，你将依靠自己的智慧来在战场上自由进出。</span>
<br><span class="rvts20"></span><span class="rvts21">战术模式</span><span class="rvts20"></span><span class="rvts21">，你遇到的敌人十分狡猾和残酷，他们会以令人厌恶的技能和偷袭来攻击你，这是探索模式和经典模式所不具备的。不适合胆小的人！</span>
<br><span class="rvts20"></span><span class="rvts21">荣誉模式</span><span class="rvts20"></span><span class="rvts21">，绝不容许失败！你只有一次保存游戏的机会，这比原本就已十分困难的战术模式更具挑战性。如果你队伍里的成员全部战死，则存档也会被删除，你的冒险也将就此终结。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">角色创建：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber8.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">选择完游戏模式后，可以进行主人公的创建，进入角色创建界面后玩家可以选择预设人物进行游戏，也可以自由创建角色。游戏一共有</span><span class="rvts20">14</span><span class="rvts21">个种族和</span><span class="rvts20">14</span><span class="rvts21">种职业供玩家选择。笔者将在此简单介绍下角色创造面板，详细内容将会在之后的系统详解中提到。</span>
<br><span class="rvts20">1.</span><span class="rvts21">可以变更种族并查看起源故事。</span>
<br><span class="rvts20">2.</span><span class="rvts21">可以查看自带的技能和天赋。</span>
<br><span class="rvts20">3.</span><span class="rvts21">可以变更职业，并查看相应职业技能。</span>
<br><span class="rvts20">4.</span><span class="rvts21">可以更改性别。</span>
<br><span class="rvts20">5.</span><span class="rvts21">可以变更</span><span class="rvts20"></span><span class="rvts21">外表</span><span class="rvts20"></span><span class="rvts21">、</span><span class="rvts20"></span><span class="rvts21">预设</span><span class="rvts20"></span><span class="rvts21">、</span><span class="rvts20"></span><span class="rvts21">天赋</span><span class="rvts20"></span><span class="rvts21">、</span><span class="rvts20"></span><span class="rvts21">标签</span><span class="rvts20"></span><span class="rvts21">、</span><span class="rvts20"></span><span class="rvts21">乐器</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">自由行动：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber9.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">游戏界面大致如图所示，其中红色框分别是：</span>
<br><span class="rvts20">1.</span><span class="rvts21">表示队伍角色的头像以及生命、魔法、</span><span class="rvts20">buff</span><span class="rvts21">和</span><span class="rvts20">debuff</span><span class="rvts21">状态等。</span>
<br><span class="rvts20">2.</span><span class="rvts21">人物功能，包含背包、菜单、地图、技能等等功能，笔者会在下面详细介绍。</span>
<br><span class="rvts20">3.</span><span class="rvts21">技能和道具快捷栏，可以用快捷键直接释放和使用。</span>
<br><span class="rvts20">4.</span><span class="rvts21">小地图，可以看到周围区域大致的地形和生物点。</span>
<br><span class="rvts20">5.</span><span class="rvts21">角色当前生命值。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">战斗场景：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber10.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">每回合你都会获得能力所对应的行动点。进行任何动作都会消耗行动点，行动点消耗完或者你无法进行需要的行动时可以点击结束回合轮到下一个角色行动（敌人或友军都有可能）。</span>
<br><span class="rvts21">游戏战斗界面大致与自由界面相同，但也有些像红框不一样的，这些分别是：</span>
<br><span class="rvts20">1.</span><span class="rvts21">头像从左到右就是敌我双方角色的行动顺序。行动顺序由主动性决定。主动性越高，行动就可能越快越频繁。</span>
<br><span class="rvts20">2.</span><span class="rvts21">用来表示该角色放出的技能或做出的动作。</span>
<br><span class="rvts20">3.</span><span class="rvts21">这里表示了角色的行动点和回合。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">手册菜单：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber11.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">按</span><span class="rvts20">L</span><span class="rvts21">键可以打开手册菜单，自动映入眼帘的是第一部分</span><span class="rvts20"></span><span class="rvts21">日志</span><span class="rvts20"></span><span class="rvts21">，日志里记录了主线剧情和支线剧情。其他的部分还包括</span><span class="rvts20"></span><span class="rvts21">地图</span><span class="rvts20"></span><span class="rvts21">、</span><span class="rvts20"></span><span class="rvts21">对话</span><span class="rvts20"></span><span class="rvts21">、</span><span class="rvts20"></span><span class="rvts21">教程</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">地图：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber12.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">按</span><span class="rvts20">M</span><span class="rvts21">键可以打开地图界面。你可以在此查看地图（地图上有重要任务、人物和物件的标志）。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">对话：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber13.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">在手册菜单中可以查看角色之间过往的对话信息。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">教程：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber14.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">在手册菜单中可以查看本游戏的教程。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">人物面板：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber15.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">按下</span><span class="rvts20">E</span><span class="rvts21">键可以打开人物面板，可以查看角色当前信息，包括主要属性、标签、装备的数值和效果等。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">手工面板：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber16.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">在这里可以合成物品，按</span><span class="rvts20">G</span><span class="rvts21">可以打开工艺面板，可以在工艺面板中使用搜集的材料制造各类物品。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">物品面板：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber17.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">按</span><span class="rvts20">B</span><span class="rvts21">或是点击功能栏图标都可以打开背包和装备界面。这是冒险中所有物品的存放处，根据玩家的力量、技能和装备效果来决定最大负重。超过一定的负重就会影响移动速度、主动性等。</span>
<br><span class="rvts30"></span>
<br><span class="rvts29">技能面板：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber18.jpg" display="block" max-width="100%"><span class="rvts30"></span>
<br><span class="rvts21">按</span><span class="rvts20">K</span><span class="rvts21">键可以打开技能界面。你会在冒险中获得大量不同的技能。根据玩家的记忆能力点数，玩家可以选择使用不超过技能槽上限的技能数量。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



